.App {
  background-color: #000000;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  text-align: center;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.Bio-block {
  background-color: #000000;
  color: rgb(255, 255, 255);
  border: 2px solid;
  border-radius: 20px;
  margin: auto;
  width: 90%;
  padding: 10px;
}

.name {
  margin-top: -5px;
  margin-bottom: -5px;
}
.nickName {
  margin-top: -5px;
  margin-bottom: -5px;
}

.social-icon-link {
  color: #ffffff;
  padding: 15px;
}

.infoSection {
  text-align: left;
  margin-bottom: 10px;
}

.redhawkImage {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 2px solid;
  border-radius: 20px;
  margin: 15px;
}

.row {
  display: flex;
}

.column1 {
  flex: 33%;
}
.column2 {
  flex: 66%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-left: 15px;
}
/* Small devices such as large phones (640px and up) */
@media only screen and (min-width: 640px) {
  .Bio-block {
    width: 70%;
  }
}
/* Medium devices such as tablets (768px and up) */
@media only screen and (min-width: 768px) {
  .Bio-block {
    width: 66%;
  }
}
/* Large devices such as laptops (1024px and up) */
@media only screen and (min-width: 1024px) {
  .Bio-block {
    width: 55%;
  }
}
/* Largest devices such as desktops (1280px and up) */
@media only screen and (min-width: 1280px) {
  .Bio-block {
    width: 50%;
  }
}

/* Largest devices such as desktops (1400px and up) */
@media only screen and (min-width: 1400px) {
  .Bio-block {
    width: 40%;
  }
}
